* {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: Roboto, sans-serif;
  font-style: normal;
}

.card {
  text-align: center;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 36px;
}

.card__title {
  color: #060b35;
  text-align: left;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  text-align: left;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card-review {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.card-review__count {
  color: #060b35;
  align-items: end;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card-price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card-price__text {
  color: #616070;
  text-align: left;
  font-size: 12px;
  line-height: 18px;
}

.card-price__amont {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .2s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  height: 38px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+1), .stars__star:nth-child(-n+2), .stars__star:nth-child(-n+3), .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
